import React from "react";
import "./index.css";
import img1 from "../../assets/img/Teamspira_online_transparente (2).png";
import img2 from "../../assets/img/img (10).png";
import img3 from "../../assets/img/img (5).png";
import Fade from 'react-reveal/Fade';
export function Home() {
  return (
    <div className="divHome0">
      <div className="divHome2">
        <img className="imgHome1" src={img1} alt="logo principal" />
        <p className="pHome1">
          O primeiro e nico curso online sobre tecnologia de comunicao pra
          quem trabalha em um workspace como o Teams.
        </p>
        <p className="pHome2">
          Existe vida alm de todos aqueles chats que voc tem que responder na
          hora, dos e-mails que voc marca como no lido e das reunies dirias
          que voc no aguenta mais.
        </p>
      </div>

      <img className="imgHome2" src={img2} alt="imagem decorativa" />

      <div className="divHome1  ">


      <Fade right>

        <img
          className="imgHome3 "
          src={img3}
          width="100%"
          alt="imagem decorativa"
          />

          </Fade>


      </div>
    </div>
  );
}
